*{
  padding: 0;
  margin: 0;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.movies{
  display: flex;
  flex-wrap: wrap;
}

.footer{
  padding: 100px;
}
.moviesStyle {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;

}
.form {
  width: 25%; 
}

.form input {
  width: 100%; 
}



